import CustomForm from "components/CustomForm";
import CustomModal from "components/CustomModal";
import useNotification from "hooks/Notifications";
import React from "react";
import { useDispatch } from "react-redux";
import { resetPassword } from "redux/Actions/Auth";
import { ERROR_MESSAGES, SNACK_VARIANT, STRINGS } from "shared/Constants";
import { NEW_PASSWORD_DATA } from "./constants";

function CreateNewPassword({ showNewPassword = false, setPasswordVisible = () => { } }) {
    const dispatch = useDispatch()
    const [msg,sendNotifications] = useNotification()
    const onSubmit = (data) => {
        const {old_password,password} = data
        dispatch(resetPassword({
            old_password,
            new_password:password
        }, (response) => {
            sendNotifications({
                msg: response.message,
                variant: SNACK_VARIANT.SUCCESS
            })
         }, (error) => {
            sendNotifications({
                msg: error || STRINGS.SOMETHING_WENT_WRONG,
                variant: SNACK_VARIANT.ERROR
            })
        }))
    };
    return (
        <CustomModal
            isOpen={showNewPassword}
            handleToggle={() => setPasswordVisible((prev) => !prev)}
        >
            <div className="confirmation_modal p-4">
                <span>{STRINGS.CREATE_NEW_PASS}</span>
                <CustomForm
                    formClass="col-md-12"
                    onSubmit={onSubmit}
                    formData={NEW_PASSWORD_DATA}
                    reCaptcha={false}
                />
            </div>
        </CustomModal>
    );
}

export default CreateNewPassword;
